import i18n from '@/i18n'

export const routes = {
	defaultHash: '',
	store: {},

	/**
	 * Initialise un vnement li pour dtecter les modifications de hash dans l'URL
	 */
	init(sDefaultHash, oStore) {
		this.defaultHash = sDefaultHash
		this.store = oStore
		window.addEventListener('hashchange', () => this.onHashChangedEvent())
	},

	/**
	 * Fonction branche sur l'vnement de modification de hash de l'URL
	 */
	onHashChangedEvent() {
		let sNewLocation = location.hash
		if (sNewLocation === '') {
			sNewLocation = this.defaultHash
		} else {
			sNewLocation = sNewLocation.substring(1)
		}
		this.changeStepFromHash(sNewLocation)
	},

	/**
	 * Il y a peut-tre mieux, mais permet de grer les boutons prcdents / suivants du navigateur
	 * ---
	 * On fait une mise  jour du hash dans le navigateur
	 * Cela dclenche l'vnement "hashchange" du navigateur
	 * Qui est dfini dans App.vue et qui dclenche changeStepFromHash ci-dessous
	 * ---
	 * @param {String} newStep
	 */
	changeStep(newStep) {
		window.location.hash = newStep
	},

	/**
	 * Modification relle du "step" en cours
	 * @param {String} newStep
	 */
	changeStepFromHash(newStep) {
		this.store.state.step = newStep
		window.scrollTo(0, 0)
	},

	/**
	 * Gre la redirection  la fin du parcours
	 */
	quitAndRedirect() {
		if (this.store.props.redirectUrl !== '' && this.store.props.redirectUrl !== undefined) {
			window.location = this.store.props.redirectUrl
		} else {
			window.location = this.getAccountUrl()
		}
	},

	/**
	 * Retourne l'adresse de Mon Compte
	 */
	getAccountUrl() {
		// TODO : rcuprer home_url ??
		if (i18n.locale === 'fr') {
			if (process.env.NODE_ENV === 'development') {
				return 'http://wedogood.local/mon-compte/'
			} else {
				return 'https://www.wedogood.co/mon-compte/'
			}
		} else {
			if (process.env.NODE_ENV === 'development') {
				return 'http://wedogood.local/my-account/'
			} else {
				return 'https://www.wedogood.co/my-account/'
			}
		}
	}
}
